import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home.component';
import { RouterMenu } from '@core/config/routing-menu';
import { ErrorComponent } from '@shared/components/error/error.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
    children: [
      { path: '', redirectTo: 'global-position', pathMatch: 'full' },
      {
        path: RouterMenu.GLOBAL_POSITION,
        loadChildren: () =>
          import('@global-position/global-position.module').then((m) => m.GlobalPositionModule),
      },
      {
        path: RouterMenu.TRANSACTIONS,
        loadChildren: () =>
          import('@transactions/transactions.module').then((m) => m.TransactionsModule),
      },
      {
        path: RouterMenu.DEPOSIT,
        loadChildren: () => import('@deposit/deposit.module').then((m) => m.DepositModule),
      },
      {
        path: RouterMenu.CASES,
        loadChildren: () => import('@cases/cases.module').then((m) => m.CasesModule),
      },
      {
        path: RouterMenu.PERSONAL_DATA,
        loadChildren: () =>
          import('@personal-data/personal-data.module').then((m) => m.PersonalDataModule),
      },
      {
        path: RouterMenu.VIRTUAL_MAILBOX,
        loadChildren: () =>
          import('@virtual-mailbox/virtual-mailbox.module').then((m) => m.VirtualMailboxModule),
      },
      {
        path: RouterMenu.HELP,
        loadChildren: () => import('@help/help.module').then((m) => m.HelpModule),
      },
      {
        path: RouterMenu.TRANSFER,
        loadChildren: () => import('@transfer/transfer.module').then((m) => m.TransferModule),
      },
      {
        path: RouterMenu.TERMS_AND_CONDITIONS,
        loadChildren: () =>
          import('@terms-conditions/terms-conditions.module').then((m) => m.TermsConditionsModule),
      },
      {
        path: RouterMenu.LEGAL_WARNING,
        loadChildren: () =>
          import('@legal-warning/legal-warning.module').then((m) => m.LegalWarningModule),
      },
      {
        path: RouterMenu.SECURITY,
        loadChildren: () =>
          import('@security-policy/security-policy.module').then((m) => m.SecurityPolicyModule),
      },
      {
        path: RouterMenu.ACCESSIBILITY,
        loadChildren: () =>
          import('@accessibility/accessibility.module').then((m) => m.AccessibilityModule),
      },
      { path: RouterMenu.ERROR, component: ErrorComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomeRoutingModule {}
