import { GlobalPosition } from '@api/models';
import { ROUTING_MENU } from './../../../core/config/routing-menu';
import { MENU_ITEMS_LEFT, MENU_ITEMS_RIGTH } from '@core/constants/menu-items.constants';
import { Router } from '@angular/router';
import { Component, OnInit, ViewChild, TemplateRef, OnDestroy, Inject } from '@angular/core';
import { RouterMenu } from '@core/config/routing-menu';
import { AuthService } from '@core/services/authentication/auth.service';
import { Subscription } from 'rxjs';
import { finalize, delay } from 'rxjs/operators';
import { StorageService, STORAGE_KEY } from '@core/services/storage/storage.service';
import { HeaderService } from '@core/services/header/header.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, OnDestroy {
  @ViewChild('confirmLogout') confirmLogoutModal: TemplateRef<any>;
  readonly headerTitle$ = this.headerService.title$.pipe(delay(1));

  menuItemsLeft = MENU_ITEMS_LEFT;
  menuItemsRigth = MENU_ITEMS_RIGTH;
  isOpenMenu: boolean;

  private modalRef: BsModalRef;
  private subs = new Subscription();

  constructor(
    private headerService: HeaderService,
    private router: Router,
    private authService: AuthService,
    private storageService: StorageService,
    private modalService: BsModalService,
  ) {}

  ngOnInit() {
    this.router.events.subscribe((rs) => {
      if (this.isOpenMenu) {
        this.isOpenMenu = false;
      }
    });
  }

  ngOnDestroy() {
    this.closeModal();
    this.subs.unsubscribe();
  }

  openModal(ref: any) {
    if (!this.modalService.getModalsCount()) {
      this.modalRef = this.modalService.show(ref);
    }
  }

  closeModal() {
    this.modalRef?.hide();
  }

  onConfirmLogout(result: boolean) {
    this.closeModal();
    if (result) {
      this.logout();
    }
  }

  logout() {
    this.subs.add(
      this.authService
        .logout()
        .pipe(
          finalize(() => {
            this.router.navigate([RouterMenu.LOGIN]);
            this.storageService.removeItem(STORAGE_KEY.TOKEN);
          })
        )
        .subscribe()
    );
  }

  openMenu() {
    this.isOpenMenu = !this.isOpenMenu;
  }

  historyBack() {
    window.history.back();
  }

  get isGlobalPosition() {
    return this.router.url.includes(RouterMenu.GLOBAL_POSITION);
  }
}
